import React, { Component } from 'react'
import "./Event.css"
export default class Event extends Component {
    render() {
        // const {link,year,title}=this.props;
        const {content}=this.props;
        return (
            <div>
                {/* <a href={link} target="_blank" rel="noreferrer" className="event">
                    {year} &nbsp; {title}
                </a> */}
                <a  href={"https://baike.baidu.com/search/none?word="+content} target="_blank" rel="noreferrer" className="event">
                    {content} 
                </a>
            </div>
        )
    }
}
