import React, { Component } from 'react'
import "./Loading.css"
export default class Loading extends Component {
    componentWillMount(){
        document.onreadystatechange=function(){
            if(document.readyState==="complete"){
                
                    document.getElementsByClassName("loading")[0].style.height="0";
                    
                
                    
            }
        }
    }
    render() {
        return (
            <div className="loading">
                <div className="loading-pic"><span>gosearch</span><div className="line"></div></div>
                
            </div>
        )
    }
}
