import React, { Component } from 'react'
import "./Clock.css"

export default class Clock extends Component {
    state={
        hour:"00",
        min:"00"
    }
    componentWillMount(){
        let date=new Date();
            let h=date.getHours().toString();
            let m=date.getMinutes().toString();
            if(h.length===1){
                h="0"+h;
            }
            if(m.length===1){
                m="0"+m;
            }
        this.setState({hour:h,
                        min:m})
        setInterval(() => {
            let date=new Date();
            let h=date.getHours().toString();
            let m=date.getMinutes().toString();
            if(h.length===1){
                h="0"+h;
            }
            if(m.length===1){
                m="0"+m;
            }
        this.setState({hour:h,
                        min:m})
                        
        }, 30000);
        
        
    }
    render() {
        return (
            <div className="clock">
                <div className="board">
                    <p id="time">{this.state.hour}:{this.state.min}</p>
                </div>
            </div>
        )
    }
}
