import React, { Component } from 'react'
import PubSub from 'pubsub-js'
import Input from '../../components/Input/Input'
import "./MusicSearch.css"
import MusicSearchDetail from './MusicSearchDetail/MusicSearchDetail'
export default class MusicSearch extends Component {
    state={songs4display:[{id:"0",songid:"",title:"",artist:"goMusic is ready to serve",album:""}]}
    componentDidMount(){
        this.token = PubSub.subscribe('songs',(_,stateObj)=>{
			this.setState({songs4display:stateObj.songs})
		})
    }
    componentWillUnmount(){
		PubSub.unsubscribe(this.token)
	}
    render() {
        const {songs4display}=this.state;
        console.log("music search detail rendering,typeof(songs4display):",typeof(songs4display) )
        return (
            <div className="music_search_box">
                <Input/>
                <div className="music_search_result">
                {
                        // datas.map( data =>{
                        //     return <Event key={data.id} {...data} />
                        // })
                        songs4display.map( i =>{
                            return <MusicSearchDetail key={i.id} {...i} />
                        })
                }
                </div>
            </div>
        )
    }
}
