import React, { Component } from 'react'
import axios from 'axios'
import { nanoid } from 'nanoid'
import PubSub from 'pubsub-js'
import "./Input.css"
export default class Input extends Component {
    state={searchKey:"",
            songs:[{id:"0",songid:"",title:"loading",artist:"loading",album:"loading"}]    
    }
    searchMusic=(key)=>{
        console.log("entering search_music")
        if(key!==""){
            axios.get("https://cloud-music-api-f494k233x-mgod-monkey.vercel.app/search?limit=20&keywords="+key).then(
            response=>{
                // console.log("response.data.result.songs.splice(0,21)",response.data.result.songs);
                let songsdata=response.data.result.songs;
                let mapped=songsdata.map(i=>{return {id:nanoid(),songid:i.id,title:i.name,artist:i.artists.name,album:i.album.name}});
                // console.log("mapped",mapped);
                this.setState({songs:mapped});
                console.log("this.state.songs",this.state.songs);
                
                PubSub.publish('songs',{songs:this.state.songs})
            },
            error=>{console.log("failed",error)}
        )
        }
        
    }
    updateSearchKey=()=>{
        const input_node_value=document.getElementById("input").value;
        console.log(input_node_value)
        this.searchMusic(input_node_value);
        
        
    }
    
    render() {
        return (
            
            <div className="input-box">
                <div className="input">
                    
                    <input id="input" type="text" />
                    <label  onClick={this.updateSearchKey}>
                    <svg t="1632772473027" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1251" width="128" height="128"><path d="M651.114667 602.048l-21.141334 16.170667 45.376 24.32a302.165333 302.165333 0 0 0 73.706667-198.314667c0-168.490667-136.576-305.088-305.088-305.088S138.88 275.712 138.88 444.202667s136.576 305.088 305.088 305.088c75.818667 0 145.024-27.690667 198.314667-73.450667l-49.514667-28.416-6.976 20.586667L820.48 902.229333l64.874667-65.493333-234.24-234.688z m-207.146667 61.376c-121.066667 0-219.2-98.154667-219.2-219.2 0-121.066667 98.133333-219.2 219.2-219.2 121.066667 0 219.2 98.133333 219.2 219.2 0 121.045333-98.154667 219.2-219.2 219.2z" p-id="1252" fill="#1296db"></path>
                    </svg>
                    </label>
                    <div className="input-underline"></div>
                </div>

            </div>
        )
    }
}
