import React, { Component } from 'react'
import axios from 'axios';
import HotSearchDetail from "../HotSearchDetail/HotSearchDetail"
import { nanoid } from 'nanoid';
import "./Hot.css"
export default class Hot extends Component {
    state={data:[{id:"0",title:"loading",description:"loading"}]}
    componentWillMount(){
        axios.get("https://cloud-music-api-f494k233x-mgod-monkey.vercel.app/search/hot/detail").then(
            response=>{
                this.setState({data:response.data.data.map(i=>{return {id:nanoid(),title:i.searchWord,description:i.content}})})
                console.log("hot_search",this.state.data);
            },
            error=>{console.log("failed",error)}
        )
    }
    render() {
        const{data}=this.state;
        return (
            
            <div className="hot_search_detail">
                <div className="hot_search_detail_board" >
                    {
                        // datas.map( data =>{
                        //     return <Event key={data.id} {...data} />
                        // })
                        data.map( i =>{
                            return <HotSearchDetail key={i.id} {...i} />
                        })
                    }
                </div>
                
            </div>
        )
    }
}
