import React, { Component } from 'react'
import "./Help.css"
export default class Help extends Component {
    render() {
        return (
            <div>
                
                <h5>简介</h5>
                <h6>这是一个基于网易云音乐api制作的播放组件</h6><br/>
                <h5>功能</h5>
                <h6>search:搜索单曲，点击搜索结果即可播放对应的音乐资源</h6>
                <h6>hot(测试版):网易云音乐平台上当前热搜，点击hot键可触发热搜数据更新</h6>
                <h6>board(即将到来):网易云音乐平台上的榜单</h6><br/>
                <h5>未来更新/优化</h5>
                <h6>优化搜索结果(去掉没有音乐资源的歌曲)</h6>
                <h6>更丰富的音乐资源</h6>
                <h6>歌词/热评功能</h6><br/>
                <h5>常见问题</h5>
                <h6>为什么看不到搜索结果(请求api数据需要一定时间，视当前网络情况而定)</h6>
                <h6>为什么页面提示没有找到资源(api数据缺失，开发者将尽快丰富可用资源)</h6>
                <h6>为什么按回车不能进行搜索(本组件预计本月支持键盘快捷指令)</h6>
            </div>
        )
    }
}
