import React, { Component } from 'react'
import "./Todayonhistory.css"
import axios from 'axios';
import Event from '../Event/Event';
import {nanoid} from 'nanoid';
export default class Todayonhistory extends Component {
    state={isactive:false,
            datas:[{id:"0",year:"loading",title:"loading",link:""}],
            month:"10",
            day:"01",
            contents:[{id:"0",content:"loading"}],
            date:"10.01"
    }
    click_history=()=>{
        console.log("before change",this.state.isactive);
        // this.setState({isactive:!(this.state.isactive)});
        this.setState({isactive:!(this.state.isactive)}, function () {
            console.log(this.state.isactive);
            if(this.state.isactive){
                const history_board_node=document.getElementsByClassName("history_board")[0];
                // history_board_node.style.animation="historyin 1s linear 1";
                history_board_node.style.cssText="animation:historyin 0.75s ease-in-out 1;animation-fill-mode: forwards;transform: scale(0);"
            }else{
                const history_board_node=document.getElementsByClassName("history_board")[0];
                history_board_node.style.cssText="animation:historyout 0.75s linear 1;animation-fill-mode: forwards;transform: scale(1);border-radius: 15px;"
            }
        });
        // console.log(this.state.isactive);
        
        // console.log(this.state.isactive);
    }
    componentWillMount(){
        let that=this;
        // http://sokyo.usa3v.cn/
        // http://localhost:3000/
        axios.get("https://api.yum6.cn/briefing/baidu.php?format=json ").then(
            response=>{
                let newdata= response.data.content.map(i=>{
                    return {id:nanoid(),content:i}
                })
                that.setState({contents:newdata,
                                date:response.data.day,
                                
                })
                console.log("success",that.state);
            },
            error=>{console.log("failed",error)}
        )
        
        // axios.get("https://query.asilu.com/today/list/").then(
        //     response=>{
        //         let newdata= response.data.data.map(i=>{
        //             return {id:nanoid(),year:i.year,title:i.title,link:i.link,type:i.type}
        //         })
        //         that.setState({datas:newdata,
        //                         day:response.data.day,
        //                         month:response.data.month,
        //         })
        //         console.log("success",that.state);
        //     },
        //     error=>{console.log("failed",error)}
        // )
    }
    render() {
        // const {datas,month,day} = this.state;
        const {contents,date} = this.state;
        // console.log("render"+datas)
        return (
            <div>
            <div className="history" onClick={this.click_history}>
                todayOnHistory
                
            </div>
            <div className="history_board"  date={date}>
                <div className="date">{date.slice(5).slice(0,2)}.{date.slice(5).slice(3,5)} &nbsp; on &nbsp; history</div>
                <div className="cancel" onClick={this.click_history}>x</div>
                <div className="event_board" >
                    {
                        // datas.map( data =>{
                        //     return <Event key={data.id} {...data} />
                        // })
                        contents.map( content =>{
                            return <Event key={content.id} {...content} />
                        })
                    }
                </div>
            </div>
            </div>
        )
    }
}
