import React, { Component } from 'react'
import PubSub from 'pubsub-js'
import axios from 'axios'
import "./MusicSearchDetail.css"
export default class MusicSearchDetail extends Component {
    play=()=>{
        console.log(this.props.songid)
        axios.get("https://cloud-music-api-f494k233x-mgod-monkey.vercel.app/song/url?id="+this.props.songid).then(
            response=>{
                console.log(response.data.data[0])
                if (response.data.data[0].url===null){
                    alert("抱歉，找不到该歌曲资源，请试试其他歌曲。")
                }else{
                    PubSub.publish('playsong',{songurl:response.data.data[0].url})
                }
                
            },
            error=>{console.log("failed",error)}
        )
        
    }
    render() {
        // const {link,year,title}=this.props;
        const {title,artist,album}=this.props;
        return (
            <div>
                {/* <a href={link} target="_blank" rel="noreferrer" className="event">
                    {year} &nbsp; {title}
                </a> */}
                <a   target="_blank" rel="noreferrer" className="MusicSearchDetail_item" onClick={this.play}>
                    {title} &nbsp;&nbsp;&nbsp;&nbsp; {artist}&nbsp;&nbsp;&nbsp;&nbsp; {album}
                </a>
            </div>
        )
    }
}
